/* ==========================================================================
# Side area style - begin
========================================================================== */

.edgtf-light-header .edgtf-page-header > div:not(.edgtf-sticky-header):not(.fixed) .edgtf-side-menu-button-opener,
.edgtf-light-header .edgtf-top-bar .edgtf-side-menu-button-opener {
	color: $header-light-color !important; //Color can be set inline from widget opener;
	
	&.opened,
	&:hover {
		color: $header-light-hover-color !important; //Color can be set inline from widget opener;
	}
}

.edgtf-dark-header .edgtf-page-header > div:not(.edgtf-sticky-header):not(.fixed) .edgtf-side-menu-button-opener,
.edgtf-dark-header .edgtf-top-bar .edgtf-side-menu-button-opener {
	color: $header-dark-color !important; //Color can be set inline from widget opener;
	
	&.opened,
	&:hover {
		color: $header-dark-hover-color !important; //Color can be set inline from widget opener;
	}
}

.edgtf-side-menu-button-opener {
	position: relative;
	display: inline-block;
	vertical-align: middle;
	color: $first-main-color;
	text-align: center;
	width: 36px;


	.edgtf-line {
		display: block;
		height: 2px;
		background-color: currentColor;
		margin: 2px 0 3px auto;
		@include edgtfTransition(width .2s ease-out, opacity .2s ease);

		&:nth-child(1),
		&:nth-child(3){
			width: 25px;
			margin-top: 0;
		}

		&:nth-child(3){
			margin-bottom: 0;
		}

		&:nth-child(2) {
			width: 36px;
		}
	}

	&.opened,
	&:hover{
		color: $first-main-color;

		.edgtf-line{
			width: 30px;
		}
	}
}

.edgtf-side-menu {
    position: fixed;
    top: 0;
	right: -405px;
	width: 405px;
    height: 100%;
    min-height: 100%;
	padding: 66px 50px 36px;
    background-color: #fff;
    text-align: left;
    overflow: hidden;
    visibility: hidden;
    z-index: 9999;
    -webkit-backface-visibility: hidden;
    box-sizing: border-box;
	box-shadow: -3px 0 3px rgba(0, 0, 0, 0.04);
    @include edgtfTransition(all .6s cubic-bezier(.77,0,.175,1));

    .edgtf-close-side-menu-holder {
        position: absolute;
	    top: 45px;
	    right: 75px;
        z-index: 1000;
	
	    .admin-bar & {
		    top: 68px;
	    }
    }

	a.edgtf-close-side-menu {
		display: block;
		color: $default-heading-color;
		font-size: 40px;
		z-index: 1000;

		&:hover {
			color: $default-text-color;
		}

		span {
			display: block;
			line-height: inherit;

			&:before {
				display: block;
				line-height: inherit;
			}
		}
	}
}

.edgtf-side-menu-slide-from-right {
	
	&.edgtf-right-side-menu-opened {
		
		.edgtf-wrapper {
			
			.edgtf-cover {
				z-index: 3000;
			}
		}
		
		.edgtf-side-menu {
			right: 0;
			visibility: visible;
		}
	}

    .edgtf-wrapper {

        .edgtf-cover {
            z-index: -1;
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            overflow: hidden;
            opacity: 0;
        }
    }
}
/* ==========================================================================
# Side area style - end
========================================================================== */